.toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #f28e1c;
    height: 56px;
}
.toolbar-nav {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.toolbar-nav-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;

}
.spacer {
    flex: 1;
}
.toolbar-nav-items li {
    padding: 0 .5rem;
}

.toolbar-logo {
    margin-left: 1rem;
}
.toolbar-logo a {
    color: #fae7d1;
    text-decoration: none;
    font-size: 1.5rem;
}
.toolbar-nav-items a {
    color: #fae7d1;
    text-decoration: none;
}

.toolbar-nav-items a:hover,
.toolbar-nav-items .active {
    color: #c07f5b;
    
}

.toolbar-nav-items .active {
    font-weight: bold;
}

@media (max-width: 768px) {
    .toolbar-nav-items {
        display: none;
    }
}

@media (min-width: 769px) {
    .toolbar-toggle-button {
        display: none;
    }
    .toolbar-logo {
        margin-left: 0;
    }
}

