.side-drawer {
    height: 100%;
    background: #fae7d1;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    width: 40%;
    max-width: 200px;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in;
}

.side-drawer.open {
    transform: translateX(0);
}
.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0;
}

.side-drawer a {
    color: #f28e1c;
    text-decoration: none;
    font-size: 1.2rem;
}

.side-drawer a:hover {
    color: #c07f5b;
}

.side-drawer .active {
    color: #c07f5b;
    font-weight: bold;
}
@media (min-width: 796px) {
    .side-drawer {
        display: none;
    }
}